<template>
  <div class="orderbox">
    <div class="query">
      <label for>订单号</label>
      <el-input class="orderinput" size="small" placeholder="请输入内容" v-model="ordernum" clearable></el-input>
      <label for>收货人</label>
      <el-input class="orderinput" size="small" placeholder="请输入内容" v-model="consignee" clearable></el-input>
      <label for>手机号</label>
      <el-input class="orderinput" size="small" placeholder="请输入内容" v-model="telnum" clearable></el-input>
      <label for>订单状态</label>
      <el-select class="orderinput" clearable size="small" v-model="value" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <br />
      <div class="timebox">
        <label for>选择时间</label>
        <el-date-picker
          size="small"
          style="margin-left:10px"
          v-model="choosetime"
          type="datetimerange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          format="yyyy 年 MM 月 dd 日 hh 时 mm 分 ss 秒"
          value-format="yyyy-mm-dd hh:mm:ss"
        ></el-date-picker>
        <el-button size="small" class="queryBtn" @click="orderQuery" type="primary">查询</el-button>
      </div>
    </div>
    <div class="ordertb">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column fixed prop="orderNo" label="订单号" width="120"></el-table-column>
        <el-table-column prop="orderTime" label="下单时间" width="180"></el-table-column>
        <el-table-column prop="phone" label="手机号" width="120"></el-table-column>
        <el-table-column prop="consignee" label="收货人" width="120"></el-table-column>
        <el-table-column prop="deliverAddress" label="配送地址" width="250"></el-table-column>
        <el-table-column prop="deliveryTime" label="送达时间" width="170"></el-table-column>
        <el-table-column prop="remarks" label="用户备注" width="120"></el-table-column>
        <el-table-column prop="orderAmount" label="订单金额" width="60"></el-table-column>
        <el-table-column prop="orderState" label="订单状态" width="120"></el-table-column>
        <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>

            <el-button @click="editClick(scope.row)" type="text" size="small">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 查看详情模态框 -->
      <el-dialog title="商品详情" :visible.sync="dialogTableVisible">
        <el-table :data="gridData">
          <el-table-column property="consignee" label="收货人"></el-table-column>
          <el-table-column property="deliverAddress" label="收获地址"></el-table-column>
          <el-table-column property="orderTime" label="下单时间"></el-table-column>
          <el-table-column property="deliveryTime" label="送达时间"></el-table-column>
          <el-table-column property="phone" label="联系电话"></el-table-column>
          <el-table-column property="orderState" label="订单状态"></el-table-column>
        </el-table>
      </el-dialog>

      <!-- 修改订单模态框 -->
      <el-dialog title="修改订单" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="订单号" :label-width="formLabelWidth">
            <el-input v-model="form.orderNo" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="下单时间" :label-width="formLabelWidth">
            <el-date-picker
              v-model="form.orderTime"
              type="date"
              placeholder="选择日期"
              format="yyyy 年 MM 月 dd 日 hh 时 mm 分 ss 秒"
              value-format="yyyy-MM-dd hh:mm:ss"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="手机" :label-width="formLabelWidth">
            <el-input v-model="form.phone" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="收货人" :label-width="formLabelWidth">
            <el-input v-model="form.consignee" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="送货地址" :label-width="formLabelWidth">
            <el-input v-model="form.deliverAddress" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="送达时间" :label-width="formLabelWidth">
            <el-date-picker
              v-model="form.deliveryTime"
              type="date"
              placeholder="选择日期"
              format="yyyy 年 MM 月 dd 日 hh 时 mm 分 ss 秒"
              value-format="yyyy-MM-dd hh:mm:ss"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="备注" :label-width="formLabelWidth">
            <el-input v-model="form.remarks" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="订单金额" :label-width="formLabelWidth">
            <el-input v-model="form.orderAmount" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="订单状态" :label-width="formLabelWidth">
            <el-select v-model="form.region" placeholder="请选择活动区域">
              <el-option label="派送中" value="派送中"></el-option>
              <el-option label="已受理" value="已受理"></el-option>
              <el-option label="已完成" value="已完成"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="saveEditOrder">确 定</el-button>
        </div>
      </el-dialog>
    </div>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="5"
        layout="total,sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
// 引入接口
import {
  API_GET_ORDER_LIST,
  API_ORDER_QUERY,
  API_GET_ORDER_INFO,
  API_EDIT_ORDER
} from "@/API/apis.js";
export default {
  data() {
    return {
      editID: "", //要修改订单的id
      // 修改订单
      dialogFormVisible: false,
      form: {
        orderNo: "",
        orderTime: "",
        phone: "",
        region: "", //订单状态
        consignee: "",
        delivery: false,
        type: [],
        resource: "",
        remarks: "", //备注
        deliverAddress: ""
      },

      // 查看订单详情
      gridData: [],
      dialogTableVisible: false,
      formLabelWidth: "120px",

      ordernum: "", //查询订单编号
      consignee: "", //收货人
      telnum: "", //电话
      orderstatus: "", //订单状态
      orderAmount: "", //订单金额

      // 订单状态筛选
      options: [
        {
          value: "已完成",
          label: "已完成"
        },
        {
          value: "已受理",
          label: "已受理"
        },
        {
          value: "派送中",
          label: "派送中"
        }
      ],
      // 订单状态select值
      value: "",
      choosetime: "", //选择的时间

      // 表格数据
      tableData: [],
      // 当前数据
      currentPage: 1,
      // 每页数据条数
      pageSize: 5,
      // 总数据条数
      total: 0
    };
  },
  // 页面组件，data初始化完成
  created() {
    // 发送请求获取订单列表
    this.getOrderList();
  },
  methods: {
    // 封装获取订单列表方法
    getOrderList() {
      API_GET_ORDER_LIST(
        this.currentPage,
        this.pageSize,
        "",
        "",
        "",
        "",
        ""
      ).then(res => {
        // 获取数据总条数
        this.total = res.data.total;
        // 数据存入data
        this.tableData = res.data.data;
        // 转换日期
        Date.prototype.format = function(fmt) {
          //
          var o = {
            "M+": this.getMonth() + 1, //月份
            "d+": this.getDate(), //日
            "h+": this.getHours(), //小时
            "m+": this.getMinutes(), //分
            "s+": this.getSeconds(), //秒
            "q+": Math.floor((this.getMonth() + 3) / 3), //季度
            S: this.getMilliseconds() //毫秒
          };
          if (/(y+)/.test(fmt))
            fmt = fmt.replace(
              RegExp.$1,
              (this.getFullYear() + "").substr(4 - RegExp.$1.length)
            );
          for (var k in o)
            if (new RegExp("(" + k + ")").test(fmt))
              fmt = fmt.replace(
                RegExp.$1,
                RegExp.$1.length == 1
                  ? o[k]
                  : ("00" + o[k]).substr(("" + o[k]).length)
              );
          return fmt;
        };

        this.tableData.forEach(item => {
          item.orderTime = new Date(item.orderTime).format(
            "yyyy-MM-dd hh:mm:ss"
          );
          item.deliveryTime = new Date(item.deliveryTime).format(
            "yyyy-MM-dd hh:mm:ss"
          );
        });
      });
    },

    // 封装订单查询方法
    orderQuery() {
      console.log(this.value);

      API_ORDER_QUERY(
        this.currentPage, //当前页
        this.pageSize, //每页显示条数
        this.ordernum, //查询订单号
        this.consignee, //收货人
        this.telnum, //电话
        this.value, //订单状态
        this.choosetime //时间
      ).then(res => {
        // 查询到结果后，把数据赋值给tabledata，重新获取数据
        this.tableData = res.data.data;
        this.total = res.data.total;
        Date.prototype.format = function(fmt) {
          //
          var o = {
            "M+": this.getMonth() + 1, //月份
            "d+": this.getDate(), //日
            "h+": this.getHours(), //小时
            "m+": this.getMinutes(), //分
            "s+": this.getSeconds(), //秒
            "q+": Math.floor((this.getMonth() + 3) / 3), //季度
            S: this.getMilliseconds() //毫秒
          };
          if (/(y+)/.test(fmt))
            fmt = fmt.replace(
              RegExp.$1,
              (this.getFullYear() + "").substr(4 - RegExp.$1.length)
            );
          for (var k in o)
            if (new RegExp("(" + k + ")").test(fmt))
              fmt = fmt.replace(
                RegExp.$1,
                RegExp.$1.length == 1
                  ? o[k]
                  : ("00" + o[k]).substr(("" + o[k]).length)
              );
          return fmt;
        };

        this.tableData.forEach(item => {
          item.orderTime = new Date(item.orderTime).format(
            "yyyy-MM-dd hh:mm:ss"
          );
          item.deliveryTime = new Date(item.deliveryTime).format(
            "yyyy-MM-dd hh:mm:ss"
          );
        });
      });
    },
    // 查看详情
    handleClick(row) {
      //row.id
      this.dialogTableVisible = true;
      // 发送请求获取商品详情
      API_GET_ORDER_INFO(row.id).then(res => {
        // console.log(res);
        // this.gridData=res.data.data
        this.gridData.splice(0, 1, res.data.data);
        Date.prototype.format = function(fmt) {
          //
          var o = {
            "M+": this.getMonth() + 1, //月份
            "d+": this.getDate(), //日
            "h+": this.getHours(), //小时
            "m+": this.getMinutes(), //分
            "s+": this.getSeconds(), //秒
            "q+": Math.floor((this.getMonth() + 3) / 3), //季度
            S: this.getMilliseconds() //毫秒
          };
          if (/(y+)/.test(fmt))
            fmt = fmt.replace(
              RegExp.$1,
              (this.getFullYear() + "").substr(4 - RegExp.$1.length)
            );
          for (var k in o)
            if (new RegExp("(" + k + ")").test(fmt))
              fmt = fmt.replace(
                RegExp.$1,
                RegExp.$1.length == 1
                  ? o[k]
                  : ("00" + o[k]).substr(("" + o[k]).length)
              );
          return fmt;
        };

        this.gridData.forEach(item => {
          item.deliveryTime = new Date(item.orderTime).format(
            "yyyy-MM-dd hh:mm:ss"
          );
          item.orderTime = new Date(item.orderTime).format(
            "yyyy-MM-dd hh:mm:ss"
          );
        });
      });
    },
    // 修改订单
    editClick(row) {
      this.editID = row.id; //修改订单的 id
      this.dialogFormVisible = true; //弹出模态框
    },
    // 保存订单
    saveEditOrder() {
      // 发送请求
      API_EDIT_ORDER(
        this.editID,//id
        this.form.orderNo,//订单号
        this.form.orderTime,//下单时间
        this.form.phone, //电话
        this.form.consignee,//收货人
        this.form.deliverAddress,//收货地址
        this.form.deliveryTime,//送达时间
        this.form.remarks,//备注
        this.form.orderAmount,//订单金额
        this.form.region//订单状态
      ).then(res => {
        if(res.data.code==0){
          this.$message({
          message: '修改订单成功',
          type: 'success',
          duration:1500
        });
         this.dialogFormVisible = false;
        }
      });
    },
    // 改变每页显示条数
    handleSizeChange(val) {
      this.pageSize = val;
      if (
        this.choosetime == "" &&
        this.value == "" &&
        this.currentPage == "" &&
        this.pageSize == "" &&
        this.ordernum == "" &&
        this.consignee == "" &&
        this.telnum == ""
      ) {
        this.getOrderList();
      } else {
        this.orderQuery();
      }
    },
    // 改变当前页
    handleCurrentChange(val) {
      this.currentPage = val;
      if (
        this.choosetime == "" &&
        this.value == "" &&
        this.currentPage == "" &&
        this.pageSize == "" &&
        this.ordernum == "" &&
        this.consignee == "" &&
        this.telnum == ""
      ) {
        this.getOrderList();
      } else {
        this.orderQuery();
      }
    },
    queryOrder() {
      console.log(this.choosetime);
    }
  }
};
</script>

<style lang="less" scoped>
.orderbox {
  background-color: #fff;
  width: 100%;
  padding-bottom: 40px;
  .query {
    padding: 20px 0 0 20px;
    .timebox {
      margin-top: 20px;
      .queryBtn {
        margin-left: 15px;
        margin-bottom: 15px;
      }
    }

    .orderinput {
      margin-top: 10px;
      width: 180px;
      margin: 0 10px 0 10px;
    }
  }
}
</style>